import Vue from 'vue'
import Router from 'vue-router'

import VueResource from 'vue-resource'

Vue.use(Router)
Vue.use(VueResource)

export const constantRouterMap = [
 

  {
    path: '/',
    redirect: '/home'
  },
  {
    path: '/home',
    meta: { title: '首页' },
    component: () => import('../views/home/index'),
    name: '首页'
  },



]

export default new Router({
  routes: constantRouterMap
})



