import Vue from "vue"
import vueResource from "vue-resource"
Vue.use(vueResource)
import { addCookie, getCookie, delCookie } from '../utils/cookies'
var domain1 ='http://api.zb.plus' //系统接口
var domain2='/apis'
// var domain1=process.env.API
// var domain2=process.env.API
export default {


  postRequest(url, params,domain, cb) {
    domain = domain1
    return new Promise((resolve, reject) => {
      Vue.http.post(
        domain + url,
        params,
        { emulateJSON: true })
        .then(cb)
        .catch((err) => {
          reject(err)
        })
    })
  },
  postRequest1(url, params,domain, cb) {
    domain = domain1
   
    return new Promise((resolve, reject) => {
      var token=  JSON.parse(getCookie('token'))
      var id =JSON.parse(getCookie('user')).userId
      Vue.http.post(
        domain + url,
        params,
        { emulateJSON: true ,
          headers: { 'X-Auth-UserId': `${id}`,
          'X-Auth-Token':`${token}` },
                                          })
        .then(cb)
        .catch((err) => {
          reject(err)
        })
    })
  },
  getRequest1(url, domain, cb) {
    return new Promise((resolve, reject) => {
        var token=  JSON.parse(getCookie('token'))
      var id =JSON.parse(getCookie('user')).userId
 
        domain = domain1
        Vue.http.get(
          domain + url,
          
          { emulateJSON: true ,
            headers: { 'X-Auth-UserId': `${id}`,
            'X-Auth-Token':`${token}` }
                                            }
        )
        .then(cb)
        .catch((err) => {
          reject(err)
        })
      
    })
  },
  getRequest(url, domain, cb) {
    return new Promise((resolve, reject) => {
  
 
        domain = domain1
        Vue.http.get(
          domain + url,
          
          { emulateJSON: true }
        )
        .then(cb)
        .catch((err) => {
          reject(err)
        })
      
    })
  },
}



